import * as React from 'react';
import { throttle } from 'lodash-es';
import { Block, Controller, useZoom, environment } from '@ali/4ever-cangjie';
import {
  useScrollableContent,
  StickerPlugin,
  CalloutPlugin,
} from '@ali/4ever-bamboo';
import { ParagraphData, ColorBlocks } from '@ali/4ever-mo';
import { useHover } from '@ali/4ever-component';
import { Popover } from '@ali/we-design-next';
import { Tooltip } from '@ali/we-design';
import { getRectOfNodeFirstText } from '@ali/4ever-utils';
import { STICKER_PANEL_COLUMMS, StickerPanel } from '@ali/4ever-plugin-sticker';
import {
  setCalloutStickerCode,
  convertKeysToContainer,
  deleteCallout,
} from '../actions';
import CalloutInnerToolbar from './InnerToolbar';
import { isCallout } from '../utils';
import { Locale } from '../types';

const { StickerItem } = StickerPlugin;
const {
  CalloutBoxWrapper,
  CalloutBox,
  ContentBox,
  StickerBox,
  StickerInnerBox,
  DEFAULTS,
  STICKER_SIZE,
  PADDING_TOP,
} = CalloutPlugin;

export interface CalloutProps {
  controller: Controller;
  node: Block<ParagraphData> | ColorBlocks;
  locale?: Locale;
  attributes: Record<string, string>;
  sticker: StickerPlugin.Sticker;
}

/**
 * 高亮区块
 * @param props
 * @returns
 */
const Callout: React.FC<CalloutProps> = (props) => {
  const { controller, node, attributes, children, sticker, locale } = props;
  const contentBoxRef = React.useRef<HTMLDivElement>();
  const stickerBoxRef = React.useRef<HTMLDivElement>();
  const disableInlineToolbar = controller.query('hasHoverToolbar', 'callout');

  const {
    bgcolor = DEFAULTS.bgcolor,
    sticker: stickerCode = DEFAULTS.stcode,
    border,
    color,
    showSticker,
  } = React.useMemo(() => {
    if (isCallout(node)) {
      const { calloutPr } = node.data;
      return {
        bgcolor: calloutPr?.backgroundColor,
        sticker: calloutPr?.stickerCode,
        showSticker: true,
      };
    } else {
      const { metadata } = (node as ColorBlocks).data;
      return { ...metadata, showSticker: Boolean(metadata.showstk) };
    }
  }, [node]);

  // 边框色默认为背景色, 使边框和文字对齐
  let borderColor = border || bgcolor;
  if (!border && bgcolor === '#FFFFFF') {
    borderColor = DEFAULTS.whiteBorder;
  }

  const ref = React.useRef<HTMLDivElement>(null);
  const [stickerTop, setStickerTop] = React.useState(0);
  const zoom = useZoom();
  const [showStickerPanel, setShowStickerTop] = React.useState(false);
  const [showInnerToolbar, setShowInnerToolbar] = React.useState(false);

  React.useEffect(() => {
    if (isCallout(node)) {
      const keys = node.nodes.map((n) => n.key);
      controller.run('onAction', convertKeysToContainer({ keys }));
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  React.useEffect(() => {
    if (!ColorBlocks.isColorBlocks(node)) {
      return;
    }
    /**
     * 背景：
     * 排版下，突然出现 node.nodes 是空数组的临时状态，可能和 task 调度有关系
     * 目前排查下来，非常难定位，暂时忽略这个 case
     *
     * hotfix：排版下，暂时不执行这里的逻辑
     *
     */
    if (node.key.indexOf('-') >= 0) {
      return;
    }
    const { nodes = [] } = node;
    if (!nodes.length || nodes.every((n) => !Block.isBlock(n))) {
      controller.run('onAction', deleteCallout({ node }));
    }
  }, [node, controller]);

  React.useEffect(() => {
    if (stickerBoxRef.current && node.nodes[0]) {
      const rect = getRectOfNodeFirstText(node.nodes[0] as Block);
      const stickerRect = stickerBoxRef.current?.getBoundingClientRect();
      if (rect && rect.height && stickerRect) {
        const newTop =
          rect.top / zoom -
          stickerRect.top / zoom -
          PADDING_TOP +
          (rect.height / zoom - STICKER_SIZE) / 2 -
          1;
        setStickerTop(Math.round(newTop));
      }
    }
  }, [node, stickerBoxRef, zoom]);

  const triggerSetSticker = React.useCallback(
    ({ code }) => {
      setShowStickerTop(false);
      controller.run(
        'onAction',
        setCalloutStickerCode({
          node,
          stickerCode: code,
        }),
      );
    },
    [controller, node, setShowStickerTop],
  );

  const updateInnerToolbar = React.useCallback(
    throttle((event: React.MouseEvent) => {
      const { target } = event;
      if (!(target instanceof HTMLElement)) {
        return;
      }
      const forceHide = target.closest('[data-istasklist]');
      const panel = target.closest('[data-color-select-panel]');
      const showIt = !panel || panel === ref.current;
      const isShow = !forceHide && showIt;
      if (showInnerToolbar !== isShow) {
        setShowInnerToolbar(isShow);
      }
    }, 100, { trailing: true }),
    [showInnerToolbar],
  );

  const handleMouseMove = React.useCallback((event: React.MouseEvent) => {
    if (environment.IS_MOBILE) {
      return;
    }
    event.persist();
    updateInnerToolbar(event);
  }, [updateInnerToolbar]);

  const handleHideInnerToolbar = React.useCallback(() => {
    updateInnerToolbar.cancel();
    setShowInnerToolbar(false);
  }, [updateInnerToolbar]);

  const enableBorderHighlight = React.useMemo(() => controller.query('enableBorderHighlight'), []);
  const [isHover, hoverHandlers] = useHover(enableBorderHighlight, {
    onMouseLeave: handleHideInnerToolbar,
  });

  const scrollContent = useScrollableContent() || window.document.body;

  // 如果不这样做，还有一种做法参考现在的 setStickerPanelVisible,
  // 通过 action 触发显示隐藏，在 controller.data 里面存状态
  // 个人不认可这种做法

  React.useEffect(() => {
    window.document.addEventListener('keydown', handleHideInnerToolbar);
    return () => {
      window.document.removeEventListener('keydown', handleHideInnerToolbar);
    };
  }, [controller, handleHideInnerToolbar]);

  const showRightToolbar = ColorBlocks.isColorBlocks(node);

  const toggleStickerPanelVisible = React.useCallback(() => {
    setShowStickerTop(!showStickerPanel)
  }, [showStickerPanel]);

  return (
    <CalloutBoxWrapper {...attributes}>
      <CalloutBox
        ref={ref}
        className="callout-box"
        data-callout-group
        data-color-select-panel
        backgroundColor={bgcolor}
        borderColor={borderColor}
        selected={false}
        {...hoverHandlers}
        onMouseMove={handleMouseMove}
        onMouseDown={() => {}}
        onClick={() => {}}
        data-hover-box="borderBox"
        isHover={isHover}
      >
        {!disableInlineToolbar && showRightToolbar && (
          <CalloutInnerToolbar
            color={color}
            bgcolor={bgcolor}
            border={border}
            locale={locale}
            showSticker={showSticker}
            controller={controller}
            node={node as ColorBlocks}
            showInnerToolbar={showInnerToolbar}
          />
        )}
        {Boolean(showSticker) && (
          // we-design-next tooltip 有 bug, 点击 popover 内的表情后不消失，这里先用 we-design 的
          <Tooltip title={locale?.switchEmoji} placement="bottom" zIndex={900}>
            <StickerBox className="callout-sticker-box" ref={stickerBoxRef}>
              <Popover
                visible={showStickerPanel}
                trigger="click"
                getContainer={() => scrollContent}
                // StickerPanel 本身有 memo
                content={
                  <StickerPanel
                    sticker={sticker}
                    columns={STICKER_PANEL_COLUMMS}
                    onClick={triggerSetSticker}
                  />
                }
                onVisibleChange={(v) => {
                  setShowStickerTop(v);
                }}
                placement="bottom"
                style={{ zIndex: 1000 }}
              >
                <StickerInnerBox style={{ top: stickerTop }}>
                  <StickerItem
                    sz={STICKER_SIZE}
                    code={stickerCode}
                    sticker={sticker}
                    onClick={toggleStickerPanelVisible}
                  />
                </StickerInnerBox>
              </Popover>
            </StickerBox>
          </Tooltip>
        )}
        <ContentBox
          data-container-block
          showSticker={showSticker}
          ref={contentBoxRef}
        >
          {children}
        </ContentBox>
      </CalloutBox>
    </CalloutBoxWrapper>
  );
};

Callout.displayName = 'Callout';

export default Callout;
